export default{
	props:{
		title:{
			type:String,
			default:''
		},
		subTitle:{
			type:String,
			default:'Unblockgrace'
		},
		textColor:{
			type:String,
			default:'#000000'
		},
		layout:{
			type:String,
			default:'center'
		},
		opacity:{
			type:Number,
			default:0
		},
		buttons:{
			type:Object
		}
	},
	data(){
		return {
			priviteOpacity:0,
			backOpacity:0.5,
			searchFocus:false
		}
	},
	watch:{
		opacity(val){
			this.priviteOpacity = (val/200).toFixed(2);
		}
	},
	mounted(){
		if(this.layout == 'search'){
			this.searchFocus = true;
		}
	},
	methods:{
		navigatorBack(obj){
			if(obj.text === '取消'){
				uni.navigateBack({
					delta:1
				})
			}else{
				uni.navigateTo({
					url:obj.url
				})
			}
		}
	}
}
